/**
 * Sistema de permisses para IA
 * Gerencia o que a IA pode e no pode fazer no FoundryVTT
 */

export class PermissionManager {
  constructor () {
    this.permissions = new Map()
    this.permissionLevels = new Map()
    this.temporaryPermissions = new Map()
    this.permissionHistory = []
    this.initialized = false
  }

  async initialize () {
    console.log('AI Assistant | Inicializando PermissionManager...')

    try {
      // Definir nveis de permisso
      this.definePermissionLevels()

      // Carregar permisses salvas
      await this.loadPermissions()

      // Configurar permisses padro se necessrio
      if (this.permissions.size === 0) {
        await this.setDefaultPermissions()
      }

      // Inicializar limpeza de permisses temporrias
      this.startTemporaryPermissionCleanup()

      this.initialized = true
      console.log('AI Assistant | PermissionManager inicializado com sucesso')
    } catch (error) {
      console.error('AI Assistant | Erro na inicializao do PermissionManager:', error)
      throw error
    }
  }

  definePermissionLevels () {
    // Definir nveis hierrquicos de permisso
    this.permissionLevels.set('NONE', {
      level: 0,
      name: 'Nenhuma',
      description: 'Sem permisses',
      permissions: {}
    })

    this.permissionLevels.set('BASIC', {
      level: 1,
      name: 'Bsico',
      description: 'Permisses bsicas de leitura e chat',
      permissions: {
        // Chat e comunicao
        sendMessage: true,
        sendWhisper: true,
        rollDice: true,

        // Consultas (somente leitura)
        queryActors: true,
        queryItems: true,
        queryScenes: true,
        queryJournal: true,
        queryMacros: true,
        queryTables: true,
        queryPlaylists: true,
        queryCompendium: true,

        // Visualizao
        viewDocuments: true
      }
    })

    this.permissionLevels.set('STANDARD', {
      level: 2,
      name: 'Padro',
      description: 'Permisses para criar e editar contedo bsico',
      permissions: {
        // Herdar do BASIC
        ...this.permissionLevels.get('BASIC').permissions,

        // Criao e edio bsica
        createItem: true,
        updateItem: true,
        deleteItem: true,

        createJournal: true,
        updateJournal: true,
        deleteJournal: true,

        createMacro: true,
        updateMacro: true,
        executeMacro: true,

        // Manipulao de atores limitada
        updateActor: true,

        // Importao de compndios
        importFromCompendium: true
      }
    })

    this.permissionLevels.set('ADVANCED', {
      level: 3,
      name: 'Avanado',
      description: 'Permisses para manipulao completa de contedo',
      permissions: {
        // Herdar do STANDARD
        ...this.permissionLevels.get('STANDARD').permissions,

        // Criao e manipulao de atores
        createActor: true,
        deleteActor: true,

        // Manipulao de cenas
        createScene: true,
        updateScene: true,
        deleteScene: true,
        activateScene: true,

        // Tabelas e playlists
        createRollTable: true,
        updateRollTable: true,
        deleteRollTable: true,
        rollTable: true,

        createPlaylist: true,
        updatePlaylist: true,
        deletePlaylist: true,
        playAudio: true,

        // Tokens e combate
        createToken: true,
        updateToken: true,
        deleteToken: true,
        manageCombat: true
      }
    })

    this.permissionLevels.set('FULL', {
      level: 4,
      name: 'Completo',
      description: 'Todas as permisses (use com cuidado)',
      permissions: {
        // Herdar do ADVANCED
        ...this.permissionLevels.get('ADVANCED').permissions,

        // Permisses administrativas
        manageUsers: true,
        modifySettings: true,
        manageModules: true,

        // Operaes perigosas
        deleteAnyDocument: true,
        executeArbitraryCode: true,
        modifyPermissions: true,

        // Sistema
        accessFileSystem: true,
        networkAccess: true
      }
    })
  }

  async loadPermissions () {
    try {
      // Simular carregamento de configuraes (FoundryVTT real usaria game.settings)
      const savedPermissions = this.getStoredPermissions() || {}
      const savedLevel = this.getStoredPermissionLevel() || 'BASIC'

      // Aplicar nvel de permisso
      await this.setPermissionLevel(savedLevel)

      // Aplicar permisses customizadas
      for (const [permission, value] of Object.entries(savedPermissions)) {
        this.permissions.set(permission, value)
      }

      console.log('AI Assistant | Permisses carregadas:', savedLevel)
    } catch (error) {
      console.warn('AI Assistant | Erro ao carregar permisses, usando padro:', error)
      await this.setDefaultPermissions()
    }
  }

  getStoredPermissions () {
    // Simular storage - em FoundryVTT real seria game.settings.get()
    try {
      return JSON.parse(localStorage.getItem('ai-assistant-permissions') || '{}')
    } catch {
      return {}
    }
  }

  getStoredPermissionLevel () {
    // Simular storage - em FoundryVTT real seria game.settings.get()
    return localStorage.getItem('ai-assistant-permission-level') || 'BASIC'
  }

  async savePermissions () {
    try {
      const permissionsObj = Object.fromEntries(this.permissions)

      // Simular storage - em FoundryVTT real seria game.settings.set()
      localStorage.setItem('ai-assistant-permissions', JSON.stringify(permissionsObj))

      // Salvar histrico
      this.permissionHistory.push({
        timestamp: Date.now(),
        permissions: { ...permissionsObj },
        changedBy: 'system' // Em FoundryVTT real seria game.user.id
      })

      // Manter apenas os ltimos 100 registros
      if (this.permissionHistory.length > 100) {
        this.permissionHistory.splice(0, this.permissionHistory.length - 100)
      }

      localStorage.setItem('ai-assistant-permission-history', JSON.stringify(this.permissionHistory))

      console.log('AI Assistant | Permisses salvas')
    } catch (error) {
      console.error('AI Assistant | Erro ao salvar permisses:', error)
      throw error
    }
  }

  async setDefaultPermissions () {
    console.log('AI Assistant | Configurando permisses padro (BASIC)')
    await this.setPermissionLevel('BASIC')
  }

  async setPermissionLevel (levelName) {
    const level = this.permissionLevels.get(levelName)
    if (!level) {
      throw new Error(`Nvel de permisso "${levelName}" no encontrado`)
    }

    // Limpar permisses atuais
    this.permissions.clear()

    // Aplicar permisses do nvel
    for (const [permission, value] of Object.entries(level.permissions)) {
      this.permissions.set(permission, value)
    }

    // Salvar nvel atual
    localStorage.setItem('ai-assistant-permission-level', levelName)

    // Salvar permisses
    await this.savePermissions()

    console.log(`AI Assistant | Nvel de permisso definido para: ${level.name}`)

    // Notificar mudana
    this.notifyPermissionChange(levelName, level.name)
  }

  checkPermission (action) {
    if (!this.initialized) {
      console.warn('AI Assistant | PermissionManager no inicializado, negando permisso')
      return false
    }

    // Verificar permisso temporria primeiro
    const tempPermission = this.temporaryPermissions.get(action)
    if (tempPermission && tempPermission.expires > Date.now()) {
      return tempPermission.granted
    }

    // Verificar permisso permanente
    const hasPermission = this.permissions.get(action) || false

    // Log da verificao (apenas para debug)
    if (!hasPermission) {
      console.debug(`AI Assistant | Permisso negada para: ${action}`)
    }

    return hasPermission
  }

  async grantPermission (action, granted = true, options = {}) {
    if (!this.checkPermission('modifyPermissions') && !options.force) {
      throw new Error('Permisso insuficiente para modificar permisses')
    }

    const oldValue = this.permissions.get(action)
    this.permissions.set(action, granted)

    // Salvar se no for temporrio
    if (!options.temporary) {
      await this.savePermissions()
    }

    console.log(`AI Assistant | Permisso ${granted ? 'concedida' : 'revogada'}: ${action}`)

    // Log da mudana
    this.logPermissionChange(action, oldValue, granted, options)

    return true
  }

  async revokePermission (action, options = {}) {
    return await this.grantPermission(action, false, options)
  }

  grantTemporaryPermission (action, duration = 300000, granted = true) { // 5 minutos padro
    const expires = Date.now() + duration

    this.temporaryPermissions.set(action, {
      granted,
      expires,
      grantedAt: Date.now(),
      grantedBy: 'system' // Em FoundryVTT real seria game.user.id
    })

    console.log(`AI Assistant | Permisso temporria ${granted ? 'concedida' : 'revogada'}: ${action} (expira em ${duration / 1000}s)`)

    // Agendar limpeza
    setTimeout(() => {
      this.temporaryPermissions.delete(action)
      console.log(`AI Assistant | Permisso temporria expirada: ${action}`)
    }, duration)

    return true
  }

  async requestPermission (action, reason = '', options = {}) {
    // Se j tem permisso, retornar true
    if (this.checkPermission(action)) {
      return { granted: true, message: 'Permisso j concedida' }
    }

    // Log da solicitao
    console.log(`AI Assistant | Solicitao de permisso: ${action} - Motivo: ${reason}`)

    // Se for auto-aprovao para permisses bsicas
    if (options.autoApprove && this.isBasicPermission(action)) {
      this.grantTemporaryPermission(action, options.duration || 300000)
      return { granted: true, temporary: true, message: 'Permisso temporria auto-aprovada' }
    }

    return { granted: false, pending: true, message: 'Solicitao enviada aos GMs' }
  }

  isBasicPermission (action) {
    const basicPermissions = [
      'rollDice', 'sendMessage', 'queryActors', 'queryItems',
      'queryScenes', 'viewDocuments'
    ]
    return basicPermissions.includes(action)
  }

  notifyPermissionChange (levelName, levelDisplayName) {
    console.log(`AI Assistant | Nvel de permisso da IA alterado para: ${levelDisplayName}`)
  }

  logPermissionChange (action, oldValue, newValue, options) {
    const logEntry = {
      timestamp: Date.now(),
      action,
      oldValue,
      newValue,
      changedBy: 'system', // Em FoundryVTT real seria game.user.id
      temporary: options.temporary || false,
      reason: options.reason || ''
    }

    // Adicionar ao histrico local
    this.permissionHistory.push(logEntry)
  }

  startTemporaryPermissionCleanup () {
    // Limpar permisses temporrias expiradas a cada minuto
    setInterval(() => {
      const now = Date.now()
      const expired = []

      for (const [action, permission] of this.temporaryPermissions.entries()) {
        if (permission.expires <= now) {
          expired.push(action)
        }
      }

      for (const action of expired) {
        this.temporaryPermissions.delete(action)
        console.log(`AI Assistant | Permisso temporria expirada: ${action}`)
      }
    }, 60000)
  }

  // Mtodos de convenincia para verificaes especficas
  canCreateActors () {
    return this.checkPermission('createActor')
  }

  canDeleteActors () {
    return this.checkPermission('deleteActor')
  }

  canModifyScenes () {
    return this.checkPermission('createScene') || this.checkPermission('updateScene')
  }

  canExecuteMacros () {
    return this.checkPermission('executeMacro')
  }

  canManageUsers () {
    return this.checkPermission('manageUsers')
  }

  // Mtodos de informao
  getCurrentPermissionLevel () {
    return this.getStoredPermissionLevel() || 'BASIC'
  }

  getAvailablePermissionLevels () {
    return Array.from(this.permissionLevels.entries()).map(([key, level]) => ({
      key,
      name: level.name,
      description: level.description,
      level: level.level
    }))
  }

  getActivePermissions () {
    const active = []
    const now = Date.now()

    // Permisses permanentes
    for (const [action, granted] of this.permissions.entries()) {
      if (granted) {
        active.push({
          action,
          type: 'permanent',
          granted
        })
      }
    }

    // Permisses temporrias
    for (const [action, permission] of this.temporaryPermissions.entries()) {
      if (permission.expires > now) {
        active.push({
          action,
          type: 'temporary',
          granted: permission.granted,
          expires: permission.expires,
          remaining: permission.expires - now
        })
      }
    }

    return active
  }

  getPermissionHistory (limit = 50) {
    return this.permissionHistory.slice(-limit)
  }

  getStats () {
    return {
      initialized: this.initialized,
      currentLevel: this.getCurrentPermissionLevel(),
      totalPermissions: this.permissions.size,
      activePermissions: this.getActivePermissions().length,
      temporaryPermissions: this.temporaryPermissions.size,
      historyEntries: this.permissionHistory.length
    }
  }

  // Mtodo para reset completo (apenas para emergncias)
  async resetToDefault () {
    console.warn('AI Assistant | Resetando permisses para padro')

    this.permissions.clear()
    this.temporaryPermissions.clear()
    this.permissionHistory = []

    await this.setDefaultPermissions()

    console.log('AI Assistant | Permisses da IA foram resetadas para o padro')
  }

  // Validao de segurana
  validatePermissionRequest (action, context = {}) {
    // Lista de aes que requerem validao extra
    const dangerousActions = [
      'deleteAnyDocument', 'executeArbitraryCode', 'modifySettings',
      'manageUsers', 'accessFileSystem', 'networkAccess'
    ]

    if (dangerousActions.includes(action)) {
      console.warn(`AI Assistant | Ao perigosa solicitada: ${action}`)
      return {
        valid: false,
        reason: 'Ao requer aprovao manual de GM',
        requiresGMApproval: true
      }
    }

    return { valid: true }
  }
}
